<div class="sort"><div class="overlay" v-if="isDropdownOpen" @click="toggleDropdown"></div><div class="sort__selected" @click="toggleDropdown">{{ currentSortLabel }}</div><transition name="fade"><ul class="sort__dropdown" v-if="isDropdownOpen"><li class="sort__item" @click="changeSortOption('default')">По умолчанию</li><li class="sort__item" @click="changeSortOption('priceDesc')">Сначала дорогие</li><li class="sort__item" @click="changeSortOption('priceAsc')">Сначала недорогие</li><li class="sort__item" @click="changeSortOption('popular')">Сначала популярные</li><li class="sort__item" @click="changeSortOption('new')">Сначала новые</li></ul></transition></div>