<div class="cart-modal" v-if="isOpen"><div class="cart-modal__overlay" @click="closeCart"></div><div class="cart-modal__content"><button class="cart-modal__close" @click="closeCart">✕</button><h2 class="cart-modal__title">Корзина</h2><p class="cart-modal__item-count">{{ cartItemCount }} {{ itemCountLabel }}</p><button class="cart-modal__clear" @click="clearCart" :disabled="cartItemCount === 0">{{ cartItemCount === 0 ? 'Корзина пуста' : 'Очистить список' }}</button><div class="cart-modal__container"><CartList :cart-items="cartItems" @decrease-quantity="decreaseQuantity" @increase-quantity="increaseQuantity" @remove-item="removeItem" @restore-item="restoreItem"></CartList><CartFooter :cart-total-price="cartTotalPrice" :cart-item-count="cartItemCount" @checkout="checkout"></CartFooter></div></div></div>